import Container from "react-bootstrap/Container";
import StyledNavBar from "./styles/NavBar.styled";
import { IconButton } from "@mui/material";
import { FiMenu } from "react-icons/fi";
import { AiOutlinePlus } from "react-icons/ai";
import { NavLink } from "react-router-dom";
const NavBar = () => {
    function HandleShowingNavigation(event) {
        event.target.parentElement
            .querySelector(".NavigationContainer")
            .classList.add("ShowedNavigation");
    }
    function HandleHidingNavigation(event) {
        event.target.parentElement.classList.remove("ShowedNavigation");
    }
    window.onscroll = () => {
        let sections = document.querySelectorAll("section");
        let navigationLinks = document.querySelectorAll(".navigationLink");
        navigationLinks.forEach((e) => {
            e.classList.remove("activeNavigation");
        });
        let counter = 0;
        sections.forEach((e) => {
            if (
                window.scrollY >= e.offsetTop &&
                window.scrollY < e.clientHeight + e.offsetTop
            ) {
                navigationLinks[counter].classList.add("activeNavigation");
            }
            counter += 1;
        });
    };
    return (
        <StyledNavBar>
            <Container>
                <div className="brand">
                    <NavLink to="/">MOUHRACH CHOAIB</NavLink>
                </div>
                <IconButton
                    onClick={(event) => {
                        HandleShowingNavigation(event);
                    }}
                >
                    <FiMenu />
                </IconButton>
                <div className="NavigationContainer">
                    <IconButton
                        onClick={(event) => {
                            HandleHidingNavigation(event);
                        }}
                    >
                        <AiOutlinePlus />
                    </IconButton>
                    <ul>
                        <li>
                            <a
                                className="navigationLink activeNavigation"
                                href="#Home"
                            >
                                Home
                            </a>
                        </li>
                        <li>
                            <a className="navigationLink" href="#Projects">
                                Projects
                            </a>
                        </li>

                        <li>
                            <a className="navigationLink" href="#About">
                                About
                            </a>
                        </li>
                        <li>
                            <a className="navigationLink" href="#Contact">
                                Contact
                            </a>
                        </li>
                    </ul>
                </div>
            </Container>
        </StyledNavBar>
    );
};

export default NavBar;
