import Button from "./Button";
import TextareaAutosize from "@mui/base/TextareaAutosize";
import StyledContact from "./styles/Contact.styled";
import Container from "react-bootstrap/Container";
import emailjs from "emailjs-com";
import Alert from "@mui/material/Alert";
import { useState } from "react";
const Contact = () => {
  const [emailstate, setEmailstate] = useState(0);
  function HandleSubmitingForm(e) {
    e.preventDefault();
    emailjs
      .sendForm(
        "service_l3rceqo",
        "template_ac46xcr",
        e.target,
        "Qf7XkOm-Bs_q2Fplf"
      )
      .then(
        (result) => {
          setEmailstate(1);
          console.log(emailstate);
        },
        (error) => {
          setEmailstate(2);
          console.log(emailstate);
        }
      );

    e.target.reset();
  }
  return (
    <StyledContact>
      <Container>
        <div className="title">
          <div className="title-inner">
            <h1>Contact</h1>
            <p>you can reach me from here</p>
          </div>
        </div>
        <div className="contact" id="Contact">
          {emailstate === 2 ? <Alert severity="error">error!</Alert> : ""}
          {emailstate === 1 ? <Alert severity="success">sucess!</Alert> : ""}
          <form
            onSubmit={(event) => {
              HandleSubmitingForm(event);
            }}
          >
            <input name="name" placeholder="Name..." type="text" />
            <input name="email" placeholder="Email..." type="text" />
            <input name="subject" placeholder="Subject..." type="text" />
            <textarea
              name="message"
              placeholder="Your email..."
              cols="30"
              rows="8"
            ></textarea>
            <button type="submit">Send</button>
          </form>
        </div>
      </Container>
    </StyledContact>
  );
};
export default Contact;
